import * as React from 'react';
import { FunctionComponent } from 'react';

// OUTSIDE COMPONENTS
import Toolbar from '@material-ui/core/Toolbar';
import Button from '@material-ui/core/Button';
import { makeStyles } from "@material-ui/core";

//ICONS
import DuplicateIcon from './../src/duplicate.svg';


export interface HealthiMedBarProps {
    majorButtons?: string;
    keyArea?: any;
    costArea?: any;
    toolbarClasses?: any;
  }

  const HealthiMedBar: FunctionComponent<HealthiMedBarProps> = ({
    majorButtons = <>
      <Button color="primary" className="text-uppercase px-4 mr-2">Delete</Button>
      <Button color="primary" variant="contained" className="text-uppercase px-4 mr-2"><img src={DuplicateIcon} className="mr-2"/>Duplicate</Button>
    </>,
    keyArea = "Chave 16",
    costArea = "5â‚¬",
    toolbarClasses
  }) => {
    const useStyles = makeStyles(() => ({
        root:{
            "&.MuiToolbar-root":{
              background: "#f2f2f2",
              bottom: 0,
              position: 'absolute',
              width: '100%',
              height: 43,
              minHeight: 43,
              display: 'flex',
              justifyContent: 'space-between',
              zIndex: 1
            },
            
            "&.MuiFormControlLabel-root":{
              marginBottom: 0,
              "& .MuiTypography-body1":{
                fontSize: 14
              },
            }
        },
        costText:{
            fontFamily: 'Open Sans',
            fontSize: 16,
        },
        digitalKey:{
            fontFamily: 'Roboto Condensed',
            fontSize: 12,
        },
      }));
    
      const classes = useStyles();
    
      return (
        <>
          <Toolbar disableGutters className={`${classes.root} ${toolbarClasses}  `}>
            <div className={`d-flex ${classes.costText}`}>
              {costArea}
            </div>
                
            <div className={`d-flex ${classes.digitalKey}`}>
              {keyArea}
            </div> 
                      
            <div className="d-flex">
              {majorButtons}
            </div>
          </Toolbar>
        </>
      );
    };

export default HealthiMedBar;
